
.form-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 50px;
}

.form {
  text-align: center;
  margin-bottom: 20px;
}

.details {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.details div {
  margin-bottom: 20px;
}

h1 {
  font-size: 24px;
  margin-bottom: 10px;
}

h2 {
  font-size: 16px;
  margin-bottom: 5px;
  text-align: justify;
}

input {
  width: 300px;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 4px;
}

.signup-btn button {
  padding: 10px 20px;
  background-color: #000;
  color: #fff;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.signup-btn button:hover {
  background-color: #333;
}

p {

  text-align: left;
  color: red;
  font-size: 13px;
}
.regi-succ{
  color: blue
}